<template>
  <layout title="管理材料信息">
      <ul>
          <li v-for="it in list" :key="it.id">
              <span>{{it.name}}</span>
              <em>{{it.remark}}</em>
              <i @click="toEdit(it)"></i>
          </li>
      </ul>
  </layout>
</template>

<script>
    import { materialListApi } from '../../apis/MaterialApi'
    import Auth from '../Auth'

    export default {
        mixins: [Auth],
        data() {
            return {
                isEdit: false,
                list: []
            }
        },
        created() {
            this.queryEvt()
        },
        computed: {
            roles() {
                return this.$store.getters['common/getRoles']
            }
        },
        methods: {
            async queryEvt() {
                let result = await materialListApi()
                this.list = result.code === 200 ? result.data.rows : []
            },
            toEdit(row) {
                if (this.handleRole('SYSTEM-01-05', 'U')) this.$router.push({path: '/material/edit', query: row})
            }
        }
    }
</script>

<style lang="less" scoped>
    @import "../../style/common.less";

    ul {
        display: block;
        height: 100%;
        overflow-x: hidden;
        padding-top: 12px;
    }
    li {
        display: block;
        height: 48px;
        padding: 7px 12px;
        margin-bottom: 12px;
        border-top: solid 1px @font-8c;
        border-bottom: solid 1px @font-8c;
        .cls-float();
        span,
        em {
            float: left;
            padding-right: 10px;
            color: @primary;
            font-size: .8rem;
            line-height: 32px;
        }
        em {
            color: @999;
            font-size: .6rem;
            font-style: normal;
        }
        i {
            float: right;
            width: 32px;
            height: 32px;
            background: url(../../images/edit-icon.png) center center / 22px 22px no-repeat;
        }
    }
</style>